input[type=search],
select {
  border-radius: 0px;
}

.students {
  text-align: center;
  padding: 0 50px;
  overflow-anchor: none;
}
.students .filter {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  animation: fadein 0.6s;
}
.students .filter .radios {
  line-height: 16px;
}
.students .filter .radios .radio {
  position: relative;
  padding-right: 25px;
}
.students .filter .radios .radio .radioInput {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}
.students .filter .radios .radio .radioBox {
  position: absolute;
  width: 12px;
  height: 12px;
  left: 35%;
  border-radius: 50%;
  border: 1px solid #19b5fe;
  background-color: #fcfcfc;
}
.students .filter .radios .radio .radioBox:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #19b5fe;
  display: none;
}
.students .filter .radios .radio .radioInput:checked ~ .radioBox:after {
  display: block;
}
.students .filter select {
  border: none;
  border-bottom: 1px solid #141414;
  font-size: 1em;
  background-color: inherit;
}
.students .filter #specialty {
  width: 190px;
}
.students .filter input[type=search] {
  border: none;
  background-color: inherit;
  border-bottom: 1px solid #141414;
  font-size: 1em;
  width: 196px;
  padding: 0 5px;
}
.students .filter input[type=search]::placeholder {
  color: #141414;
  text-align: center;
}
.students .filter input[type=search]:focus {
  outline-color: #00c0ff;
  outline-width: 1px;
}
.students .schedule .day {
  margin-bottom: 20px;
  animation: fadein 0.8s;
}
.students .schedule .day .header {
  display: flex;
  justify-content: space-between;
  font-size: 1.11em;
  font-weight: 500;
  margin-bottom: 15px;
}
.students .schedule .day .header span {
  text-transform: capitalize;
}
.students .schedule .day .lesson {
  text-align: left;
  display: flex;
  font-size: 1em;
  padding-bottom: 10px;
  margin-bottom: 12px;
  border-bottom: 1px solid #e9e9e9;
}
.students .schedule .day .lesson:last-child {
  border-bottom: none;
  padding-bottom: 2px;
}
.students .schedule .day .lesson .start {
  padding-left: 10px;
  width: 70px;
  font-weight: 300;
  color: #c4c4c4;
}
.students .schedule .day .lesson .start .num {
  margin-bottom: 5px;
}
.students .schedule .day .lesson .subject {
  font-weight: 500;
  margin-bottom: 5px;
}
.students .schedule .day .lesson .lecturer {
  font-weight: 500;
  color: #5581f1;
  margin-right: 14px;
}
.students .pagination {
  cursor: pointer;
  border-radius: 2px;
  border: 2px solid #00c0ff;
  background-color: #00c0ff;
  color: #fefefe;
  font-weight: 500;
  padding: 0.5em 1.2em;
  transition: all 0.2s ease;
}
.students .pagination:hover {
  transition: all 0.2s ease;
  background-color: inherit;
  color: #00c0ff;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media screen and (max-width: 1325px) {
  .students {
    padding: 0 30px;
  }
}
@media screen and (max-width: 1250px) {
  .students {
    padding: 0 5px;
  }
  .students .filter {
    flex-direction: column;
    align-items: center;
  }
  .students .filter .radios {
    margin-bottom: 10px;
  }
  .students .filter select {
    background-color: inherit;
    width: 100%;
    margin-bottom: 10px;
    padding: 2px 0;
  }
  .students .filter #specialty {
    width: 100%;
  }
  .students .filter input[type=search] {
    background-color: inherit;
    width: 100%;
    padding: 5px;
  }
  .students .filter input[type=search]::placeholder {
    text-align: left;
  }
  .students .schedule .day .header {
    font-size: 1.05em;
  }
  .students .schedule .day .lesson {
    font-size: 0.95em;
  }
  .students .schedule .day .lesson .start {
    width: 60px;
  }
  .students .schedule .day .lesson .subjectAndLecturer {
    flex: 1;
  }
  .students .schedule .day .lesson .lecturer {
    margin-right: 0;
  }
  .students .schedule .day .lesson span {
    display: block;
    margin-bottom: 2px;
  }
}