@import "../../../../colors.scss";

input[type="search"],
select {
  border-radius: 0px;
}

.students {
  text-align: center;
  padding: 0 50px;
  overflow-anchor: none;
  .filter {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
    animation: fadein 0.6s;
    .radios {
      line-height: 16px;
      .radio {
        position: relative;
        padding-right: 25px;
        .radioInput {
          position: absolute;
          width: 1px;
          height: 1px;
          overflow: hidden;
          clip: rect(0, 0, 0, 0);
        }
        .radioBox {
          position: absolute;
          width: 12px;
          height: 12px;
          left: 35%;
          border-radius: 50%;
          border: 1px solid $radio;
          background-color: #fcfcfc;
        }
        .radioBox:after {
          content: "";
          position: absolute;
          top: 2px;
          left: 2px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: $radio;
          display: none;
        }

        .radioInput:checked ~ .radioBox:after {
          display: block;
        }
      }
    }

    select {
      border: none;
      border-bottom: 1px solid $black;
      font-size: 1em;
      background-color: inherit;
    }
    #specialty {
      width: 190px;
    }
    input[type="search"] {
      border: none;
      background-color: inherit;
      border-bottom: 1px solid $black;
      font-size: 1em;
      width: 196px;
      padding: 0 5px;
      &::placeholder {
        color: $black;
        text-align: center;
      }
      &:focus {
        outline-color: $light-blue;
        outline-width: 1px;
      }
    }
  }
  .schedule {
    .day {
      margin-bottom: 20px;
      animation: fadein 0.8s;
      .header {
        display: flex;
        justify-content: space-between;
        font-size: 1.11em;
        font-weight: 500;
        margin-bottom: 15px;
        span {
          text-transform: capitalize;
        }
      }
      .lesson {
        text-align: left;
        display: flex;
        font-size: 1em;
        padding-bottom: 10px;
        margin-bottom: 12px;
        border-bottom: 1px solid #e9e9e9;
        &:last-child {
          border-bottom: none;
          padding-bottom: 2px;
        }
        .start {
          padding-left: 10px;
          width: 70px;
          font-weight: 300;
          color: $gray;
          .num {
            margin-bottom: 5px;
          }
        }
        .subject {
          font-weight: 500;
          margin-bottom: 5px;
        }
        .lecturer {
          font-weight: 500;
          color: $dark-blue;
          margin-right: 14px;
        }
      }
    }
  }
  .pagination {
    cursor: pointer;
    border-radius: 2px;
    border: 2px solid $light-blue;
    background-color: $light-blue;
    color: $white;
    font-weight: 500;
    padding: 0.5em 1.2em;
    transition: all 0.2s ease;
    &:hover {
      transition: all 0.2s ease;
      background-color: inherit;
      color: $light-blue;
    }
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 1325px) {
  .students {
    padding: 0 30px;
  }
}

@media screen and (max-width: 1250px) {
  .students {
    padding: 0 5px;
    .filter {
      flex-direction: column;
      align-items: center;
      .radios {
        margin-bottom: 10px;
      }
      select {
        background-color: inherit;
        width: 100%;
        margin-bottom: 10px;
        padding: 2px 0;
      }
      #specialty {
        width: 100%;
      }
      input[type="search"] {
        background-color: inherit;
        width: 100%;
        padding: 5px;
        &::placeholder {
          text-align: left;
        }
      }
    }
    .schedule {
      .day {
        .header {
          font-size: 1.05em;
        }
        .lesson {
          font-size: 0.95em;
          .start {
            width: 60px;
          }
          .subjectAndLecturer {
            flex: 1;
          }
          .lecturer {
            margin-right: 0;
          }
          span {
            display: block;
            margin-bottom: 2px;
          }
        }
      }
    }
  }
}
